.contact-overlay {
	display: none;
	position: fixed;
	height: 100%;
	width: 100%;
	top: 0;
	bottom: 0;
	background: rgba(0,0,0,0.25); }
.contact-modal {
	display: none;
	top: 1rem;
	position: fixed;
	width: 75%;
	height: auto;
	background: $white;
	margin-left: 12.5%;
	border: 4px solid $dark-grey;
	border-radius: 4px;
	overflow: scroll;
	margin-top: 10%;
	z-index: 999;
	ul {
		list-style: none;
		padding: 0;
		li {
			text-align: center;
			font-size: 2rem;
			a {
				text-decoration: none;
				color: $dark-grey; }
			a,
			p {
				display: inline-block; }
			i {
				margin-right: 0.75rem; }
			.close-contact {
				text-decoration: underline;
				color: $blue; }
			&:first-child a p {
				margin-top: 0; } } }
	@media screen and (max-width: $break-medium) {
		width: 85%;
		margin-left: 7.5%;
		ul li {
			font-size: 1.25rem; } } }
.contact-overlay.open {
	display: block; }
.contact-modal.open {
	display: block; }

